/* base */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-size: 1.125rem;
  line-height: 1.2;
  color: #333;
  margin: 0;
  letter-spacing: 0.025em;
  overflow-x: hidden;
}

@media only screen and (min-width: 479px) {
    body {
        font-size: 1.875rem;
    }
}

a {
  text-decoration: none;
  color: #fff;
}

img {
    max-width: 100%;
    width: 100%;
}

h1,
h2 {
    font-weight: 800;
}

h1 {
    color: #fff;
    font-size: 1.875rem;
}

h2 {
    font-size: 1.5rem;
}

@media only screen and (min-width: 1024px) {
    h1 {
        font-size: 4rem;
    }

    h2 {
        font-size: 3rem;
    }
}


@media only screen and (min-width: 1600px) {
    h1 {
        font-size: 7.5rem;
    } 
}


/* animations */
@keyframes fadeInRight {
    0% {
        opacity: 0;
        transform: translateX(-20%);
    }
    100% {
      opacity: 1;
      transform: translateX(0%);
    }
}

/* utils */
.mb {
    margin-bottom: 3.125rem;
}

@media only screen and (min-width: 479px) {
    .mb {
        margin-bottom: 6.25rem;
    }
}

.nm {
    margin: 0;
}
  
.overflow-hidden {
    overflow-y: hidden;
}

/* header */
.header {
    position: fixed;
    left: 0;
    top: 1.5rem;
    width: 100%;
    display: flex;
    z-index: 10;
    justify-content: space-between;
    align-items: center;
    padding: 0 2.5rem;
}

@media only screen and (min-width: 1024px) {
    .header {
        top: 1.875rem;
        justify-content: center;
    }
}

.header__logo {
    display: none;
    height: 0px;
}

.header__logo--alt {
    display: block;
    height: auto;
}

.header__logo img {
    width: 184px;
}

@media only screen and (min-width: 1024px) {
    .header__logo img {
        width: auto;
    }

    .header__logo {
        display: block;
        height: auto;
    }

    .header__logo--alt {
        display: none;
    }
}

.header__nav {
    width: 978px;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.5rem;
}

.header a {
    font-size: 1rem;
    font-weight: bold;
}
@media only screen and (max-width: 1024px) {
    /* mobile nav */
    .header__nav {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        top: -1.5rem;
        left: 0;
        width: 100%;
        height: 100vh;
        opacity: 0;
        transition: opacity 0.2s;
        pointer-events: none;
    }
    
    .header__nav.active {
        background-color:#fff;
        opacity: 1;
        z-index: 1;
        pointer-events: auto;
    }
    
    .header__nav a {
        color: #333;
        opacity: 0;
        display: block;
        font-size: 2rem;
    }
    
    .header__nav.active a {
        padding-bottom: 0.5rem;
        opacity: 1;
    }
    
    .header__nav.active a:nth-child(3) {
        padding-bottom: 0;
    }
    
    .header__nav.active a:first-child {
        animation: fadeInRight 0.4s ease forwards;
    }
    
    .header__nav.active a:nth-child(2) {
        animation: fadeInRight 0.6s ease forwards;
    }
    
    .header__nav.active a:nth-child(4) {
        animation: fadeInRight 0.8s ease forwards;
    }
    
    .header__nav.active a:last-child {
        animation: fadeInRight 1.2s ease forwards;
    }
}


/* burger */
.burger {
    z-index: 1000;
    cursor: pointer;
}

@media only screen and (min-width: 1024px) {
    .burger {
        display: none;
    }
}

.burger__line {
    position: relative;
    background-color: #fff;
    width: 1.5rem;
    height: 0.2rem;
    transition: 240ms;
}

.burger__line:not(:last-child) {
    margin-bottom: 5px;
}

.burger__line:nth-child(2) {
    transition: all 0.2s ease;
}

.burger.active .burger__line:nth-child(1) {
    transform: translateY(8.2px) rotate(45deg);
    background-color: #333;
}

.burger.active .burger__line:nth-child(2) {
    transform: translateY(8.2px) rotate(45deg);
    opacity: 0;
}

.burger.active .burger__line:nth-child(3) {
    transform: translateY(-8.2px) rotate(-45deg);
    background-color: #333;
}

/* hero */
.hero {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-image: url('../img/gym.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    text-align: center;
    height: 292px;
    padding-bottom: 50px;
}


@media only screen and (min-width: 479px) {
    .hero {
        height: 545px;
        align-items: center;
        padding-bottom: 0;
    }
}

/* gallery */
.gallery {
    text-align: center;
}

.gallery__container {
    width: 1650px;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 40px;
}

@media only screen and (min-width: 1600px) {
    
    .gallery__container {
        padding: 0 25px;
    }
}

.gallery h2 {
    margin-bottom: 1em;
}

@media only screen and (min-width:  1600px) {
    .gallery h2 {
        margin-bottom: 0.2em;
    }
}

.gallery__items { 
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -40px;
}

@media only screen and (min-width:  1600px) {
    .gallery__items  {
        margin: 0 -25px;
    }
}

.gallery__items div {
    flex: 1 0 100%;
    padding: 40px;
}

@media only screen and (min-width: 1024px) {
    .gallery__items div  {
        flex: 1 0 33%;
    }
}

@media only screen and (min-width: 1600px) {
    .gallery__items div  {
        padding: 25px;
    }
}